<template>
  <div class="container">
  </div>
</template>

<script>

export default {
  name: "room",
  data() {
    return {
      stream: null
    }
  },
  async mounted() {
    this.stream = await navigator.mediaDevices.getUserMedia({audio:true})
    setTimeout(()=>{
      let interval = setInterval(()=>{
        if(this.stream){
          console.log(this.$route.params.id)
          this.$store.getters.PEER.call(this.$route.params.id,this.stream)
          clearInterval(interval)
        }
      },100)
    },500)
  }
}
</script>

